@font-face {
  font-family: "Golos-Text";
  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_Regular.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_Regular.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_Regular.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_Regular.eot") format("eot");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Golos-Text";
  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_DemiBold.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_DemiBold.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_DemiBold.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_DemiBold.eot") format("eot");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Golos-Text";
  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_Bold.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_Bold.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_Bold.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_Bold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/Montserrat/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Bold.ttf") format("ttf"), url("/fonts/Montserrat/Montserrat-Bold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Alegreya-Sans";
  src: local("Alegreya-Sans"), url("../fonts/Alegreya-Sans/AlegreyaSans-Bold.woff2") format("woff2"), url("../fonts/Alegreya-Sans/AlegreyaSans-Bold.woff") format("woff"), url("../fonts/Alegreya-Sans/AlegreyaSans-Bold.ttf") format("ttf"), url("/fonts/Alegreya-Sans/AlegreyaSans-Bold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
.news__wrapper, .help {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Golos-Text", sans-serif;
  font-size: 15px;
  line-height: 1.74;
  font-weight: 400;
  color: #404040;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

.page {
  overflow: hidden;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 82px;
}
.header__logo {
  display: flex;
  width: 100%;
  max-width: 520px;
  margin-right: 24px;
}
.header__logo-img {
  margin-right: 24px;
}
.header__logo-text {
  font-size: 11px;
  line-height: 1.58;
  color: #8F9090;
}

.logo {
  height: 32px;
}

.actions-menu {
  display: flex;
  align-items: center;
}
.actions-menu__list {
  display: flex;
}
.actions-menu__link {
  margin-right: 32px;
  font-size: 14px;
  line-height: 1.58;
  color: #003154;
}
.actions-menu__link--feedback {
  padding-left: 23px;
  position: relative;
}
.actions-menu__link--feedback:before {
  content: "";
  display: block;
  width: 17px;
  height: 20px;
  margin-top: -1px;
  background-image: url("../images/letter-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 0;
  left: 0;
}
.actions-menu__search {
  padding: 0;
  border: 0;
  background: none;
}
.actions-menu__search:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("../images/search-icon.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.site-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 54px;
}
.site-nav__link {
  padding-bottom: 7px;
  padding-top: 7px;
  font-size: 15px;
  line-height: 1.48;
}
.site-nav__link--subnav {
  padding-right: 15px;
  position: relative;
}
.site-nav__link--subnav:after {
  content: "";
  display: block;
  width: 10px;
  height: 6px;
  background-image: url("../images/arrow-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
}

.intro {
  background: linear-gradient(90deg, rgb(247, 247, 248) 70.4%, rgb(96, 192, 195) 70.4%);
}
.intro__slider {
  overflow: visible;
}
.intro__slide {
  display: flex;
  align-items: center;
  height: 600px;
}
.intro__content {
  width: 50%;
  max-width: 486px;
  margin-right: auto;
}
.intro__decorative {
  display: flex;
  margin-bottom: 12px;
}
.intro__decorative-item:not(:last-child) {
  margin-right: 16px;
}
.intro__decorative-icon {
  width: 40px;
  height: 40px;
}
.intro__title {
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 33px;
  line-height: 1.16;
  color: #003154;
}
.intro__description {
  margin-bottom: 54px;
  font-size: 18px;
  line-height: 1.62;
}
.intro__img {
  width: 50%;
}
.intro__slide-img {
  width: 588px;
  height: 440px;
  margin-left: auto;
  object-fit: cover;
  filter: drop-shadow(4px 8px 24px rgba(0, 0, 0, 0.2)) drop-shadow(16px 28px 64px rgba(0, 0, 0, 0.16));
}
.intro__swiper-pagination.swiper-pagination {
  bottom: 32px;
}
.intro__swiper-pagination > .swiper-pagination-bullet {
  background: #003154;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 0;
  font-family: inherit;
  font-size: 15px;
  line-height: 1.48;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}
.btn--standard {
  padding-top: 11px;
  padding-bottom: 11px;
  border: 1px solid #003154;
  border-radius: 2px;
}
.btn--primary {
  padding-left: 58px;
  padding-right: 58px;
  color: #003154;
}
.btn--second {
  padding-left: 31px;
  padding-right: 31px;
  color: #fff;
  background-color: #003154;
}
.btn--link {
  padding-right: 16px;
  color: #1E1E1E;
  position: relative;
}
.btn--link:after {
  content: "";
  width: 10px;
  height: 6px;
  background-image: url("../images/arrow-icon.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 9px;
  right: 0;
  z-index: 1;
  transform: rotate(-90deg);
}

.section--primary {
  padding: 80px 0;
}
.section--second {
  padding: 64px 0;
}
.section__title {
  font-size: 29px;
  line-height: 1.16;
  font-weight: 600;
  color: #003154;
}
.section__suptitle {
  font-size: 13px;
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #888888;
}

.main-menu {
  display: flex;
}
.main-menu__header {
  margin-right: 118px;
}
.main-menu__title {
  margin-bottom: 22px;
}
.main-menu__nav {
  display: flex;
  margin-bottom: 1px;
}
.main-menu__list {
  width: 200px;
  margin-right: 40px;
  padding-left: 16px;
  border-left: 1px solid rgb(225, 225, 225);
}
.main-menu__item:not(:last-child) {
  margin-bottom: 18px;
}
.main-menu__link {
  font-size: 17px;
  line-height: 1.58;
  color: #1E1E1E;
}

.statistics {
  color: #fff;
  background-color: #003154;
}
.statistics__description {
  width: 100%;
  max-width: 788px;
  margin: 0 auto 101px;
  font-size: 23px;
  line-height: 1.3;
  text-align: center;
}
.statistics__paragraph > sup {
  font-size: 58%;
}
.statistics__description-list {
  display: flex;
  justify-content: space-between;
  margin: 0;
}
.statistics__description-group {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}
.statistics__description-group:before {
  content: "";
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  z-index: -1;
}
.statistics__description-group--turnover:before {
  width: 94px;
  height: 96px;
  background-image: url("../images/rhombus.svg");
  top: -14px;
  left: -47px;
}
.statistics__description-group--amount:before {
  width: 84px;
  height: 80px;
  background-image: url("../images/heart.svg");
  top: -8px;
  left: -40px;
}
.statistics__description-group--area:before {
  width: 80px;
  height: 80px;
  background-image: url("../images/circle.svg");
  top: -8px;
  left: -42px;
}
.statistics__description-group--countries:before {
  width: 88px;
  height: 71px;
  background-image: url("../images/crystal.svg");
  top: -1px;
  left: -49px;
}
.statistics__description-term {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 51px;
  line-height: 1.1;
}
.statistics__description-term > span {
  font-weight: 700;
  font-size: 23px;
}
.statistics__description-details {
  margin: 0;
  line-height: 1.48;
}

.about-us {
  display: flex;
  height: 450px;
}
.about-us__item {
  width: 50%;
}
.about-us__img {
  width: 100%;
}
.about-us__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 80px;
}
.about-us__text {
  width: 100%;
  max-width: 418px;
}
.about-us__title {
  margin-bottom: 9px;
}
.about-us__subtitle {
  margin-bottom: 16px;
}
.about-us__description {
  margin-bottom: 28px;
}
.about-us__description > sup {
  font-size: 58%;
}

.help__text {
  grid-column: 1/span 3;
  margin-top: auto;
  margin-bottom: 21px;
}
.help__title {
  margin-bottom: 8px;
}
.help__subtitle {
  margin-bottom: 16px;
}

.help-element {
  display: flex;
  flex-direction: column;
  padding: 0 32px 40px 32px;
  height: 612px;
  background: linear-gradient(180deg, #fff 23.5%, #F2F6FB 23.5%);
  position: relative;
}
.help-element:nth-child(2) {
  grid-column: 5/span 4;
}
.help-element:nth-child(3) {
  grid-column: 9/span 4;
}
.help-element__title {
  margin-bottom: 16px;
  margin-top: auto;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.1;
  color: #1E1E1E;
}
.help-element__description {
  margin-bottom: 39px;
}
.help-element__btn {
  margin-right: auto;
}
.help-element__img {
  width: 384px;
  height: 308px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.news {
  padding-top: 64px;
  padding-bottom: 80px;
}
.news__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}
.news-article {
  display: flex;
  flex-direction: column;
  background: #F7F7F8;
}
.news-article:nth-of-type(1) {
  grid-column: 1/span 4;
}
.news-article:nth-of-type(2) {
  grid-column: 5/span 4;
}
.news-article:nth-of-type(3) {
  grid-column: 9/span 4;
}
.news-article__content {
  padding: 23px;
  order: 2;
}
.news-article__title {
  margin-bottom: 6px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.52;
  color: #000103;
}
.news-article__title-link > sup {
  font-size: 58%;
}
.news-article__tags {
  display: flex;
  margin-bottom: 16px;
}
.news-article__tags-item {
  font-size: 12px;
  line-height: 1.58;
  position: relative;
}
.news-article__tags-item:not(:last-child):after {
  content: "";
  display: inline-block;
  width: 3px;
  height: 3px;
  margin: 0 6px;
  position: relative;
  top: -2px;
  border-radius: 50%;
  background-color: #D0D0D0;
}
.news-article__date {
  color: #8F9090;
}
.news-article__description {
  margin-bottom: 9px;
  font-size: 14px;
  line-height: 1.58;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
.news-article__img {
  order: 1;
  border-radius: 2px;
}